import MutationTypes from './MutationTypes'
export default {
  [MutationTypes.CHANGE_CITY] (state, payload) {
    state.city = payload.city
    try {
      localStorage.setItem('city', payload.city)
    } catch (error) {}
  },
  [MutationTypes.CHANGE_LETTER] (state, payload) {
    state.letter = payload.letter
  }
}



